form{
    margin-top: 3rem;
}
form input[name="city"]{
    background-color: transparent;
    color:white;
    border: 0;
    border-bottom: 2px solid whitesmoke;
    border-radius: 0;
}
form input:focus{
    background-color: transparent!important;
    box-shadow: none!important;
    color: white!important; 
}
form input
